.app {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: large;
 line-height: 2em;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


h1{
  font-family: Georgia, 'Times New Roman', Times, serif;
  text-align: center;
  color: purple;
}

.footer{
  background-color: rgb(64, 235, 115);
  position:relative;
  height:auto;
  bottom: 0;
  width: 100%;
  text-align: center;

}



